@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --body-14-medium: Montserrat;

  /* font sizes */
  --body-14-medium-size: 14px;
  --body-16-medium-size: 16px;
  --heading-h3-size: 20px;
  --heading-h2-size: 32px;
  --heading-h1-1-size: 68px;

  /* Colors */
  --text-4: #f5f5f5;
  --color-whitesmoke-100: #f2f2f2;
  --color-white: #fff;
  --text-gray-02: #414449;
  --text-gray-01: #2e3134;
  --color-black: #000;
  --text-gray-03: #7d848a;
  --primary-grean-01: #326c3c;

  /* Gaps */
  --gap-5xl: 24px;
  --gap-5xs: 8px;
  --gap-base: 16px;
  --gap-9xs: 4px;
  --gap-61xl: 80px;
  --gap-13xl: 32px;
  --gap-xl: 20px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-89xl: 108px;
  --padding-29xl: 48px;
  --padding-61xl: 80px;
  --padding-3xs: 10px;
  --padding-base: 16px;
  --padding-13xl: 32px;
  --padding-5xs: 8px;
  --padding-21xl: 40px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-5xs: 8px;
  --br-lg: 18px;
  --br-31xl: 50px;

  /* Effects */
  --effect: 0px 25px 40px rgba(0, 0, 0, 0.03);
  --cta: 0px 15px 24px rgba(255, 192, 0, 0.3);
  --shadow-01: 0px 4px 24px rgba(106, 106, 106, 0.08);
}
